p{
    color: white;
}


.hero-label{
    font-size: 2em;
    float: right;
    text-transform:uppercase;
    font-family: monospace;
}
.hero-image{
    width: 30%;
    max-width: 50px;
    float: right;
    
}
.hero{
    padding: 5rem;
    text-align: center;
}
.navcon{
    padding-top: 3em;
    text-align: center;
}

.fade{
    animation: fadein 850ms;
}
@keyframes fadein {
    from {opacity: 25%;}
    to {opacity: 100%;}
}
.clink{
    background-color: rgb(8, 8, 8);
    transition: 250ms ease-in;
}
.clink:hover,
.clink:focus
{
    opacity: 75%;
    margin-left: 2%;
}
.link{
    text-decoration: none;
    color: rgb(255, 255, 255);
}
.click-about{
    text-align: center;
    margin: 5em;
    animation: fadein 1s;
}
.nav-button{
    font-size: 1.5em;
    font-style: italic;
    margin-left: 1em;
    text-transform: uppercase;
    border: none;
    transition: 200ms ease-in-out;
}
.nav-button:hover
{
    opacity: 50%;
    transform: scale(1.1);
}
.website-about{
    text-transform: capitalize;
    font-style: italic;
}
.tag{
    padding: 0.8em;
}